  transition: background-color 0.3s ease-in-out, color 0.3s ease-in-out, padding-top 0.3s ease-in-out, padding-bottom 0.3s ease-in-out, transform 0.5s ease-out; }
  .header:not(.header_fixed) {
    color: var(--color-text-light); }
  .header_fixed {
    --color: var(--color-text-dark);
    --color-logo: #3D065F;
    background-color: var(--color-header-fixed-bg);
    padding: 15px 0; }
  .header_hidden {
    transition: background-color 0.3s ease-in-out, color 0.3s ease-in-out, padding-top 0.3s ease-in-out, padding-bottom 0.3s ease-in-out, transform 0.35s ease-in;
    transform: translateY(-100%); }
  .header__logo {
    color: var(--color-logo);
    transition: color 0.3s ease-in-out; }
  .header__nav-link {
    color: var(--color); }
  .header__container {
    display: grid;
    grid-template-columns: 1fr auto 1fr;
    justify-content: center;
    align-items: center; }
  .header__button {
    display: flex;
    justify-content: flex-end;
    color: var(--color-btn-magic); }
    .header__button .button {
      font-size: 14px;
      line-height: 16px;
      min-height: 48px;
      padding: 8px 37px;
      border-radius: 4px;
      background-color: var(--color-btn-magic-background);
      font-weight: 700;
      transition: background-color 0.15s ease-in-out; }
      .header__button .button[data-touch], .header__button .button[data-hover] {
        background-color: var(--color-btn-magic-hover-background); }
      .header__button .button[data-click] {
        background-color: var(--color-btn-magic-click-background); }

/*========== Footer ==========*/
.footer {
  flex-shrink: 0; }

/*========== Preloader ==========*/
.preloader {
  position: fixed;
  width: 100%;
  height: 100%;
  z-index: 1000;
  background-color: var(--color-preloader-background);
  display: flex;
  align-items: center;
  justify-content: center; }
  .preloader__wrapper {
    overflow: hidden; }
  .preloader__element {
    font-family: "Qanelas", sans-serif;
    line-height: 1.3; }
    .preloader__element span:first-child {
      color: var(--color-accent-1);
      background: var(--gradient-1);
      -webkit-background-clip: text;
      -webkit-text-fill-color: transparent; }
    .preloader__element span:last-child {
      color: var(--color-accent-2); }
